import { styled } from 'styled-components';

import Icon from '#components/Icon';
import { IconSizes } from '#components/Icon/Icon.types';

import {
  GAP,
  ABOVE,
  BELOW,
  OPEN,
  ARROW_HEIGHT,
  ARROW_WIDTH,
} from './PopupTip.constants';

type TriggerProps = {
  $size: IconSizes;
};

export const StyledIcon = styled(Icon)``;

export const Wrapper = styled.div`
  position: relative;
  --tooltip-color: ${({ theme }) => theme.color.dark.canvas.card};
`;

/**
 * @todo replace with IconButton
 */
export const Trigger = styled.div<TriggerProps>`
  --size: ${({ theme, $size }) => theme.dimension.icon[$size].width}px;
  display: flex;
  position: relative;
  width: var(--size);
  height: var(--size);
  align-items: center;
  justify-content: space-around;
  cursor: pointer;

  &::after {
    position: absolute;

    /* recommended by WCAG */
    top: 50%;
    left: 50%;
    width: ${({ theme }) => theme.dimension.minTouchArea};
    height: ${({ theme }) => theme.dimension.minTouchArea};
    background: transparent;
    content: '';
    transform: translate(-50%, -50%);
  }

  &::before {
    position: absolute;
    left: 50%;
    z-index: 101;
    box-sizing: border-box;
    border-right: solid ${ARROW_WIDTH}px transparent;
    border-left: solid ${ARROW_WIDTH}px transparent;
    content: none;
    transform: translateX(-50%);
  }

  &.${OPEN}, &:hover {
    color: ${({ theme }) => theme.color.actionBlack.alt};
  }

  &.${OPEN}::before {
    content: '';
  }

  /* text box below */
  &.${BELOW}::before {
    /* nudge by 1px to fix gap bug */
    top: calc(100% + ${GAP}px - ${ARROW_HEIGHT}px);
    border-top: solid 0 transparent;
    border-bottom: solid ${ARROW_HEIGHT + 1}px var(--tooltip-color);
  }

  /* text box above */
  &.${ABOVE}::before {
    top: -${GAP}px;
    border-top: solid ${ARROW_HEIGHT}px var(--tooltip-color);
    border-bottom: solid 0 transparent;
  }
`;

export const Body = styled.div`
  position: absolute;
  z-index: 100;
  width: 160px;
  max-width: 50vw;
  box-sizing: border-box;
  padding: ${({ theme }) => theme.spacing.x1}px;
  border-radius: 6px;
  background-color: var(--tooltip-color);
  box-shadow: 1px 2px 4px 0px ${({ theme }) => theme.color.veil.base};
  color: ${({ theme }) => theme.color.ink.inverted};
  font: normal 14px / 18px ${({ theme }) => theme.fontFamily.secondaryFont};
`;
