/*  min distance between edge of viewport and popupTip on any side  */
export const MARGIN = 10;

/*  distance between body and trigger */
export const GAP = 12;

/*  height of arrow  */
export const ARROW_HEIGHT = 10;

/*  width of arrow */
export const ARROW_WIDTH = 12;

/*  class designating that text box should be shown above trigger */
export const ABOVE = 'above';

/*  class designating that text box should be shown below trigger */
export const BELOW = 'below';

/*  class designating that text box is open */
export const OPEN = 'open';
