import { RefObject } from 'react';

import { MARGIN, GAP, ABOVE, BELOW, OPEN } from './PopupTip.constants';

export function calculateTriggerClass(show: boolean, orientation: string) {
  if (show) {
    if (orientation === ABOVE) {
      return `${OPEN} ${ABOVE}`;
    }

    return `${OPEN} ${BELOW}`;
  }

  return '';
}

type PositionAndOrientation = [number, number, string];

export const calculatePosition = (
  triggerRect: DOMRect,
  bodyRect: DOMRect,
  viewportWidth: number,
  viewportHeight: number,
): PositionAndOrientation => {
  const triggerRectTop = triggerRect.top;
  const triggerRectRight = triggerRect.right;
  const triggerRectLeft = triggerRect.left;

  const triggerRectWidth = triggerRect.width;
  const triggerRectHeight = triggerRect.height;

  const bodyRectWidth = bodyRect.width;
  const bodyRectHeight = bodyRect.height;

  let top: number;
  let orientation: string;

  const idealOffset = (bodyRectWidth - triggerRectWidth) / 2;
  const maxLeftOffset = triggerRectLeft - MARGIN;
  const maxRightOffset = viewportWidth - MARGIN - triggerRectRight;

  let offset = idealOffset;

  if (idealOffset > maxLeftOffset) {
    offset = maxLeftOffset;
  }

  if (idealOffset > maxRightOffset) {
    offset = offset + (idealOffset - maxRightOffset);
  }

  const maxLowerSpace = viewportHeight - triggerRectTop - MARGIN;
  const popupTipHeight = triggerRectHeight + GAP + bodyRectHeight;

  if (popupTipHeight > maxLowerSpace) {
    //  ABOVE
    orientation = ABOVE;
    top = -1 * (bodyRectHeight + GAP);
  } else {
    //  BELOW
    orientation = BELOW;
    top = triggerRectHeight + GAP;
  }

  const left = -1 * offset;

  return [left, top, orientation];
};

export const NULL_DOM_RECT = {
  top: 0,
  bottom: 0,
  left: 0,
  right: 0,
  width: 0,
  height: 0,
};

export function getBB(ref: RefObject<Element>) {
  if (ref.current !== null) {
    return ref.current.getBoundingClientRect();
  }

  return NULL_DOM_RECT as DOMRect;
}
